import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class DashboardService {

  private apiBase = 'http://ec2-3-21-105-226.us-east-2.compute.amazonaws.com:3123/dashboard';

  constructor(private http: HttpClient) { }

  public getAll = () => {
    return this.http.get(`${this.apiBase}/getAll`);
  }

  public getUnjoined = () => {
    return this.http.get(`${this.apiBase}/getUnjoined`);
  }

  public joinRoom = (_roomId: string) => {
    return this.http.post(`${this.apiBase}/join`, { roomId: _roomId});
  }

  public deleteRoom = (_roomId: string) => {
    return this.http.post(`${this.apiBase}/delete`, { roomId: _roomId});
  }
}
